<template>
    <div>
         <div>
             <v-card class="card">
              <v-text-field label="Thought Title" hide-details="auto" v-model="title" class="input"></v-text-field>
    <v-text-field label="Your name" v-model="name" class="input"></v-text-field>
    <v-textarea
      autocomplete="email"
      label="Thought Body"
      v-model="body"
      class="input"
    ></v-textarea>
     <v-btn v-on:click="savePost">
         Submit
     </v-btn>
             </v-card>

  </div>
    </div>
</template>

<script>
import DataService from '../PostService';
export default {
    name:'AddPost',
    data() {
        return {
            title:'',
            body:'',
            name:'',
            error:''
        }
    },
    methods: {
       async savePost(){
          try {
            await DataService.creatPost(this.title,this.body, this.name)
            this.text = '';
            this.body = '';
            this.name = '';
          } catch (err) {
               this.error = err;
          }
        }
    },
}
</script>

<style scoped>
  input{
      padding:10xp 10px;
  }
</style>